import { createRouter, createWebHistory } from 'vue-router'

import Intro from '@/views/Intro.vue'
import Login from '@/views/login.vue'
import Form from '@/views/form.vue'
import Menu from '@/views/menu.vue'
import List from '@/views/list1.vue'
import List2 from '@/views/list2.vue'
import Done from '@/views/done.vue'
import Quizz from '@/views/quizz.vue'
import Resultados from '@/views/resultados.vue'

const routes = [
      {path: '/', name: 'Intro', component: Intro},
      {path: '/Login', name: 'Login', component: Login},
      {path: '/Form', name: 'Form', component: Form},
      {path: '/Menu', name: 'Menu', component: Menu},
      {path: '/List', name: 'List', component: List},
      {path: '/Tematicos', name: 'Tematicos', component: List2},
      {path: '/Done', name: 'Done', component: Done},
      {path: '/Quizz', name: 'Quizz', component: Quizz},
      {path: '/Resultados', name: 'Resultados', component: Resultados}
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
