import store from '../store/index'
import { reactive, toRefs } from "vue";
import router from '@/router';
import axios from 'axios';

export default function(){
    const data = reactive({
        pregunta: [],
        vGeneral: null,
        btnType: [],
        idGeneral: null,
        rColor: [],
        icons: [],
        btnType: [],
        registro: [],
        nota: [],
        fCounter: null,
        foto: null,
        texto: null,
        calificacion: null,
        fotos: null
    })

    data.rColor = store.getters.res;
    data.icons = store.getters.getIcons;
    data.btnType = store.getters.getButtons;
    data.registro = store.getters.getRegistro;
    data.nota = store.getters.getNota;

    const relenti = async() => {
        data.pregunta = store.state.Filtradas[data.vGeneral]
        if(data.pregunta != '[]'){
            data.fotos = require('@/images/' + data.pregunta.foto.replace('@/images/', ''))
        }
    }

    const load = (valor) =>{
        data.vGeneral = valor;
        relenti();
    
        // 
        if(data.registro[data.vGeneral] == 'true'){
            reset();
        }else{
            //
            for(let c = 0; c < data.rColor.length; c++){
                let ic = data.rColor[c].replace('r', '') -1;
                if(data.pregunta.correcta == data.rColor[c]){
                    document.getElementById(data.rColor[c]).style.color = "green"
                    data.icons[ic] = 'M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z'
                }else{
                    document.getElementById(data.rColor[c]).style.color = "red"
                    data.icons[ic] = 'M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z'
                }
            }
        }
    }

    const reset = () =>{
        for(let c = 0; c < data.rColor.length; c++){
            document.getElementById(data.rColor[c]).style.color = "black"
            data.icons[c] = 'M4.475 5.458c-.284 0-.514-.237-.47-.517C4.28 3.24 5.576 2 7.825 2c2.25 0 3.767 1.36 3.767 3.215 0 1.344-.665 2.288-1.79 2.973-1.1.659-1.414 1.118-1.414 2.01v.03a.5.5 0 0 1-.5.5h-.77a.5.5 0 0 1-.5-.495l-.003-.2c-.043-1.221.477-2.001 1.645-2.712 1.03-.632 1.397-1.135 1.397-2.028 0-.979-.758-1.698-1.926-1.698-1.009 0-1.71.529-1.938 1.402-.066.254-.278.461-.54.461h-.777ZM7.496 14c.622 0 1.095-.474 1.095-1.09 0-.618-.473-1.092-1.095-1.092-.606 0-1.087.474-1.087 1.091S6.89 14 7.496 14Z'
        }
    }

    const add = () =>{
        if(data.vGeneral < 29){
            data.vGeneral ++
            data.pregunta = store.state.Filtradas[data.vGeneral]
            data.fotos = require('@/images/' + data.pregunta.foto.replace('@/images/', ''))
            //
            if(data.registro[data.vGeneral] == 'true'){
                reset();
            }else{
                //
                for(let c = 0; c < data.rColor.length; c++){
                    let ic = data.rColor[c].replace('r', '') -1;
                    if(data.pregunta.correcta == data.rColor[c]){
                        document.getElementById(data.rColor[c]).style.color = "green"
                        data.icons[ic] = 'M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z'
                    }else{
                        document.getElementById(data.rColor[c]).style.color = "red"
                        data.icons[ic] = 'M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z'
                    }
                }
            }
        }
    }

    const quit = () =>{
        if(data.vGeneral > 0){
            data.vGeneral --
            data.pregunta = store.state.Filtradas[data.vGeneral]
            data.fotos = require('@/images/' + data.pregunta.foto.replace('@/images/', ''))
            //
            if(data.registro[data.vGeneral] == 'true'){
                reset();
            }else{
                //
                for(let c = 0; c < data.rColor.length; c++){
                    let ic = data.rColor[c].replace('r', '') -1;
                    if(data.pregunta.correcta == data.rColor[c]){
                        document.getElementById(data.rColor[c]).style.color = "green"
                        data.icons[ic] = 'M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z'
                    }else{
                        document.getElementById(data.rColor[c]).style.color = "red"
                        data.icons[ic] = 'M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z'
                    }
                }
            }
        }
    }

    const check = (id) => {
        
        if(id == data.pregunta.correcta && data.btnType[data.vGeneral] == 'btn-secondary' && data.btnType[data.vGeneral] != 'btn-danger'){
            data.btnType[data.vGeneral] = 'btn-success'
            // 'C' = 'Correcto / 'V' = 'Vacio' / 'I' = 'Incorrecto'.
            data.nota[data.vGeneral] = 'C'
        }else if(data.btnType[data.vGeneral] != 'btn-success'){
            data.btnType[data.vGeneral] = 'btn-danger'
            // 'C' = 'Correcto / 'V' = 'Vacio' / 'I' = 'Incorrecto'.
            data.nota[data.vGeneral] = 'I'
            data.fCounter++
        }
        //
        for(let c = 0; c < data.rColor.length; c++){
            let ic = data.rColor[c].replace('r', '') -1;
            if(data.pregunta.correcta == data.rColor[c]){
                document.getElementById(data.rColor[c]).style.color = "green"
                data.icons[ic] = 'M12.736 3.97a.733.733 0 0 1 1.047 0c.286.289.29.756.01 1.05L7.88 12.01a.733.733 0 0 1-1.065.02L3.217 8.384a.757.757 0 0 1 0-1.06.733.733 0 0 1 1.047 0l3.052 3.093 5.4-6.425a.247.247 0 0 1 .02-.022Z'
            }else{
                document.getElementById(data.rColor[c]).style.color = "red"
                data.icons[ic] = 'M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z'
            }
        }
        data.registro[data.vGeneral] = 'false'
        //
        let empty = false
        for(let c = 0; c < data.nota.length; c++){
            if(data.nota[c] == 'V'){
                empty = true;
            }
        }
        if(empty == true){
            data.calificacion = '#vacio'
        }else{
            data.calificacion = '#nota'
        }
    }

    const finish = () => {
        if(data.calificacion == '#nota'){
            reset();
            store.state.FailC = data.fCounter
            for(let c = 0; c < data.registro.length; c++){
                store.state.registro[c] = 'true';
            } 
            for(let c = 0; c < data.btnType.length; c++){
                store.state.btnType[c] = 'btn-secondary';
            }    
            if(data.fCounter > 3){
                data.foto = require('@/assets/fail.png')
                data.texto = 'Has fallado este test pero no pasa nada sigue intentandolo'
            }else{
                data.foto = require('@/assets/correct.png')
                data.texto = 'Enhorabuena completaste este test con exito vamos a por el siguiente!!'
            }
        }
    }

    const back = () => {
        router.push('/List')
    }

    const insertData = () => {
            router.push('/List')
        // axios.post("http://localhost:5000/API/sendTest", {
        //     user: store.state.User,
        //     numero: store.state.Test,
        //     fCounter: store.state.FailC,
        //     tryC: store.state.TryC,
        //     // fallos:
        // }).then(
        //         router.push('/List')
        //     )
    }
    

    return { ...toRefs(data), load, add, quit, check, finish, back, insertData}
}