import router from "@/router";
import axios from "axios";
import { reactive, toRefs } from "vue";
import store from "../store/index";
export default function(){
    const data = reactive({
        userForm: null,
        passForm: null,
        infoLabel: null,
        claseCarga: 'fw-bold mb-0',
        roleCarga: '',
        visualText: '',
        debugCounter: 0
    })

    data.infoLabel = 'Al pulsar en "Entrar" aceptas nuestros terminos y servicios'

    const login = async() => {
        data.debugCounter ++
        //Limpiar campo userForm de inyeccion sql
        data.userForm = data.userForm.replace("'", "")
        data.userForm = data.userForm.replace('"', "")
        data.userForm = data.userForm.replace("", "")
        data.userForm = data.userForm.replace("=", "")
        data.userForm = data.userForm.replace("/", "")
        data.userForm = data.userForm.replace(">", "")
        data.userForm = data.userForm.replace("<", "")
        //Limpiar passForm nombre de inyeccion sql
        data.passForm = data.passForm.replace("'", "")
        data.passForm = data.passForm.replace('"', "")
        data.passForm = data.passForm.replace("", "")
        data.passForm = data.passForm.replace("=", "")
        data.passForm = data.passForm.replace("/", "")
        data.passForm = data.passForm.replace(">", "")
        data.passForm = data.passForm.replace("<", "")

        let post = {
            userForm: data.userForm,
            passForm: data.passForm
            };
        await axios.post("http://localhost:5000/API/sendUser", post).then(
            checkUser()
        )
    }

    const checkUser = async() => {
        data.claseCarga = 'spinner-border text-primary'
        data.roleCarga = 'status'
        data.visualText = 'visually-hidden'
         await axios.get("http://localhost:5000/API/user").then((usuario) => {
            if(usuario.data != '' && usuario.data[0].clave == data.passForm){
                store.state.User = usuario.data[0].dni
                router.push('/Menu');
            }else{
                if(data.debugCounter == 5){
                    setTimeout(() => {
                        location.reload();
                    }, 500);
                }
                data.infoLabel = 'Usuario o contrasea incorrectos'
                data.claseCarga = 'fw-bold mb-0'
                data.roleCarga = ''
                data.visualText = ''
                document.getElementById('infoLabel').style.color = 'red';
            }
        })
    }

    return { ...toRefs(data), login}
}