import router from "@/router";
import axios from "axios";
import { reactive, toRefs } from "vue";
import store from "../store/index";
export default function(){
    const data = reactive({
        min: '',
        max: ''
    })

    //Cambiar peticiones a filtro de datos ya consultados.
    const getQuestions = async(min, max, test) => {
        data.min = min
        data.max = max
        store.state.Test = test
        store.state.Filtradas = []

        for(let i = 0; i < store.state.Preguntas.length; i++){
            if(store.state.Preguntas[i].id > min && store.state.Preguntas[i].id < max){
                store.state.Filtradas.push(store.state.Preguntas[i])
            }
        }

        router.push('/Quizz')
    }

    const reload = async() => {
        await axios.get('http://localhost:5000/API/preguntas').then((preguntas) => {
            store.state.Preguntas = preguntas.data;
        })
    }

    // const listData = () => {
    //     axios.get('http://localhost:5000/API/getTest').then((tests) => {
    //         console.log(tests.data)
    //     })
    // }

    return { ...toRefs(data), getQuestions, reload}
}