import router from "@/router";
import axios from "axios";
import { reactive, toRefs } from "vue";
export default function(){
    const data = reactive({
        nombre: null,
        apellidos: null,
        telefono: null,
        email: null,
        autoescuela: null,
        provincia: null,
        codPostal: null,
        llamar: null,
        correo: null,
    })

    const contact = () => {
        if(data.nombre == null || data.apellidos == null || data.telefono == null || data.email == null || data.autoescuela == null || data.provincia == null|| data.codPostal == null ){
            event.preventDefault();
            window.alert("debes rellenar todos los datos");
        }else{
            //Limpiar campo nombre de inyeccion sql
            data.nombre = data.nombre.replace("'", "")
            data.nombre = data.nombre.replace('"', "")
            data.nombre = data.nombre.replace("", "")
            data.nombre = data.nombre.replace("=", "")
            data.nombre = data.nombre.replace("/", "")
            data.nombre = data.nombre.replace(">", "")
            data.nombre = data.nombre.replace("<", "")
            //Limpiar campo apellidos de inyeccion sql
            data.apellidos = data.apellidos.replace("'", "")
            data.apellidos = data.apellidos.replace('"', "")
            data.apellidos = data.apellidos.replace("", "")
            data.apellidos = data.apellidos.replace("=", "")
            data.apellidos = data.apellidos.replace("/", "")
            data.apellidos = data.apellidos.replace(">", "")
            data.apellidos = data.apellidos.replace("<", "")
            //Limpiar campo telefono de inyeccion sql
            data.telefono = data.telefono.replace("'", "")
            data.telefono = data.telefono.replace('"', "")
            data.telefono = data.telefono.replace("", "")
            data.telefono = data.telefono.replace("=", "")
            data.telefono = data.telefono.replace("/", "")
            data.telefono = data.telefono.replace(">", "")
            data.telefono = data.telefono.replace("<", "")
            //Limpiar campo email de inyeccion sql
            data.email = data.email.replace("'", "")
            data.email = data.email.replace('"', "")
            data.email = data.email.replace("", "")
            data.email = data.email.replace("=", "")
            data.email = data.email.replace("/", "")
            data.email = data.email.replace(">", "")
            data.email = data.email.replace("<", "")
            //Limpiar campo autoescuela de inyeccion sql
            data.autoescuela = data.autoescuela.replace("'", "")
            data.autoescuela = data.autoescuela.replace('"', "")
            data.autoescuela = data.autoescuela.replace("", "")
            data.autoescuela = data.autoescuela.replace("=", "")
            data.autoescuela = data.autoescuela.replace("/", "")
            data.autoescuela = data.autoescuela.replace(">", "")
            data.autoescuela = data.autoescuela.replace("<", "")
            //Limpiar campo codPostal de inyeccion sql
            data.codPostal = data.codPostal.replace("'", "")
            data.codPostal = data.codPostal.replace('"', "")
            data.codPostal = data.codPostal.replace("", "")
            data.codPostal = data.codPostal.replace("=", "")
            data.codPostal = data.codPostal.replace("/", "")
            data.codPostal = data.codPostal.replace(">", "")
            data.codPostal = data.codPostal.replace("<", "")
            //
            console.log(data)
            axios.post("http://localhost:5000/API/sendForm", data).then(
                router.push('/Done')
            )
        }
    }

    return { ...toRefs(data), contact}
}