import router from "@/router";
import store from "@/store";
import axios from "axios";
import { reactive, toRefs } from "vue";

export default function(){

    const data = reactive({
        chargeaClass: null,
    })

    const getQuestions = async() => {
        data.chargeaClass = 'spinner-border spinner-border-sm'
        await axios.get('http://localhost:5000/API/preguntas').then((preguntas) => {
            store.state.Preguntas = preguntas.data;
            router.push('/List')
        })
        
    }

    return { ...toRefs(data), getQuestions}
}