      pregunta,
      fotos,
      load,
      add,
      quit,
      check,
      finish,
      back,
      insertData,
      btnType,
      ic,
      icons,
      calificacion,
      foto,
      fCounter,
      texto,
      vGeneral,
    } = functions();
    return {
      pregunta,
      fotos,
      load,
      add,
      quit,
      check,
      finish,
      back,
      insertData,
      btnType,
      ic,
      icons,
      calificacion,
      foto,
      fCounter,
      texto,
      vGeneral,
    };
  },

  mounted() {
    this.load(0);
    if (this.pregunta == undefined) {
      router.push("/List");
    }
  },
};
</script>

<style>
#pregunta {
