  padding: 5%;
}

#cont-pregunta {
  padding: 5%;
  width: 100%;
}

#r1,
#r2,
#r3 {
  padding-top: 1.5%;
}

#foto {
  margin-top: 3%;
  height: 87%;
}

.foto {
  width: 100%;
  height: 100%;
}

#enunciado {
  margin-bottom: 3%;
}
</style>
